var mixin = {
  methods: {
    // p = props
    // jsonProps
    // jsonProps props
    setProps (p) {
      if (this.jsonProps !== null) {
        if (this.jsonProps[p] !== undefined) {
          return this.jsonProps[p]
        } else { 
          return this[p]
        }
      } else {
        return this[p]
      }
    }
  }
}

export default mixin
